import type { BoolResult, DateProps, ShouldSyncProps } from '../definitions';
import { RookSDK } from '../plugin';
import { isValidDate } from '../utils/isValidDate';

export const RookSummaries = {
  shouldAndroidSyncFor: (props: ShouldSyncProps): Promise<BoolResult> => {
    isValidDate(props.date);
    return RookSDK.shouldSyncFor(props);
  },

  syncSleepSummary: (props: DateProps): Promise<BoolResult> => {
    isValidDate(props.date);
    return RookSDK.syncSleepSummary(props);
  },

  syncBodySummary: (props: DateProps): Promise<BoolResult> => {
    isValidDate(props.date);
    return RookSDK.syncBodySummary(props);
  },

  syncPhysicalSummary: (props: DateProps): Promise<BoolResult> => {
    isValidDate(props.date);
    return RookSDK.syncPhysicalSummary(props);
  },

  reSyncFailedSummaries: (): Promise<BoolResult> => {
    return RookSDK.reSyncFailedSummaries();
  },

  syncSummaries: (): Promise<BoolResult> => {
    return RookSDK.syncSummaries();
  },
};
